const userInfo = createSlice({
  name: 'userInfo',
  initialState: [
    { id: 'qwe08040', password: 'qwe08040@', name: 'qwe', number: '010-5468-6935' },
  ],
  reducers: {
    plusInfo(state, action) {
      //    false
      const result = state.some((user) => {
        return (
          user.id === action.payload.id &&
          user.password === action.payload.password &&
          user.name === action.payload.name &&
          user.number === action.payload.number
        )
      })

      if (!result) {
        state.push(action.payload)
      }
    },
    newChange(state, action) {
      const result = state.findIndex((userInfo) => { return userInfo.id == action.payload.id })

      if (result !== -1) {
        return produce(state, (draftState) => {
          draftState[result] = {
            id: action.payload.id,
            password: action.payload.password,
            name: action.payload.name,
            number: action.payload.number
          };
        })
      }

      return state;

    }
  }
})

