import './Header.css';
import { Link, useNavigate, useLocation } from 'react-router-dom'
import { useSelector } from 'react-redux';
import { useEffect, useState } from 'react';

function UpHeader() {
    const userInfo = useSelector((state) => { return state.userInfo })
    const loginData = JSON.parse(sessionStorage.getItem('LoginData'));
    // console.log('loginData : '+loginData.id)
    const navigate = useNavigate();

    const [access, setAccess] = useState(false);

    useEffect(()=>{
        if(loginData){
            const user = userInfo.some((userInfo)=> userInfo.id === loginData.id)
            setAccess(user);
            console.log('access : ' + user)
        }else{
            setAccess(false);
        }
    },[userInfo, loginData])

    const Logout = () => {
        sessionStorage.clear();

        navigate('/');
    }


    return (
        <div className="upheade_bar">
            <div className="upheade">
                <Link className="cursorPonter" to="/"><img src={process.env.PUBLIC_URL + '/main_img/esr_logo2.png'} /></Link>
                {
                    access ? (
                        <>
                            <div className='upheade_nav'>
                                <Link className="upheade_text cursorPonter" to="/Mypage"> </Link>
                                <Link className="upheade_text cursorPonter" to="/Order"> </Link>
                                <Link className="upheade_text cursorPonter" to="/Lovepage"> </Link>
                                <Link className="upheade_text cursorPonter" onClick={Logout} to="/"></Link>
                            </div>
                        </>
                    ) : (
                        <>
                            <div className='upheade_nav'>
                                <Link className="upheade_text cursorPonter" to="/login"> </Link>
                                <Link className="upheade_text cursorPonter" to="/join"></Link>
                            </div>
                        </>
                    )
                }
            </div>
        </div>

    )
}

