import { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate, useLocation } from 'react-router-dom'
import { plusInfo } from '../store.js';
import './login.css';

function Login() {
    const dispatch = useDispatch();
    const state = useSelector((state) => { return state.userInfo })
    console.log(state)
    const navigate = useNavigate();

    const [id, setId] = useState('');
    const [password, setPassword] = useState('');

    //  
    const [isLogin, setIsLogin] = useState(false);

    const onChangeId = ((e) => {
        const currentId = e.target.value;
        setId(currentId)
    })

    const onChangePassword = ((e) => {
        const currentPassword = e.target.value;
        setPassword(currentPassword)
    })

    const clickLogin = (() => {
        const user = state.some(userInfo => userInfo.id === id && userInfo.password === password);

        if (user) {
            const userData = {
                id: id,
                pw: password
            };
            sessionStorage.setItem('LoginData', JSON.stringify(userData))
            setIsLogin(true);
            navigate('/');
        } else {
            alert("     .")
            setIsLogin(false);
        }
    })


    return (
        <div className="login_wrap">
            <div className='login_title'> ESR .</div>

            <div className='login_input_wrap'>
                <div className="login_input login_id">
                    <input id="id" value={id} onChange={onChangeId} placeholder="" />
                    <img className='cursorPinter' src={process.env.PUBLIC_URL + '/login_img/delete_button.png'} />
                </div>

                <div className='login_input login_pw'>
                    <input type="text" onChange={onChangePassword} placeholder="" />
                    <img className='cursorPinter' src={process.env.PUBLIC_URL + '/login_img/delete_button.png'} />
                </div>
            </div>

            <div className='login_keep_find'>
                <div className='login_keep cursorPinter'>
                    <img src={process.env.PUBLIC_URL + '/login_img/id_save_1.png'} />
                     
                </div>

                <div className='login_find cursorPinter'>
                    <div>  </div>
                    <div> </div>
                </div>
            </div>

            <div className='login_button_wrap'>
                <button type="button" onClick={clickLogin} className="login_button cursorPinter"></button>
            </div>

        </div>

    )
}

function Join() {
    const dispatch = useDispatch();
    const state = useSelector((state) => { return state.userInfo });

    const [id, setId] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPw, setConfirmPw] = useState('');
    const [name, setName] = useState('');
    const [number, setNumber] = useState('');

    //  
    const [isId, setIsId] = useState(false);
    const [isPassword, setIsPassword] = useState(false);
    const [isConfirmPw, setIsConfirmPw] = useState(false);
    const [isName, setIsName] = useState(false);
    const [isNumber, setIsNumber] = useState(false);

    //   
    const [idMessage, setIdMessage] = useState("");
    const [PasswordMessage, setPasswordMessage] = useState("");
    const [ConfirmPwMessage, setConfirmPwMessage] = useState("");
    const [NameMessage, setNameMessage] = useState("");
    const [NumberMessage, setNumberMessage] = useState("");

    const duplicationId = () => {
        const idRegExp = /^[a-zA-z0-9]{4,12}$/;
        const result = state.some((userInfo) => { return (userInfo.id === id) });

        if (!idRegExp.test(id)) {
            alert(' .');
        } else if (result) {
            alert(' .');
        } else {
            alert('  .');
            setIsId(true);
        }
    }

    const onChangeId = useCallback((e) => {
        const currentId = e.target.value;
        setId(currentId)
        setIsId(true);
    }, [])

    const onClickJoin = () => {
        if (isId && isPassword && isConfirmPw && isName && isNumber) {

            const newUserData = {
                id: id,
                password: password,
                name: name,
                number: number
            }
            dispatch(plusInfo(newUserData))
            sessionStorage.clear();
        }
    }

    const onChangePassword = useCallback((e) => {

        const currentPassword = e.target.value;
        setPassword(currentPassword)
        const passwordRegExp = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,25}$/;

        if (currentPassword === "") {
            setPasswordMessage("");
        } else if (!passwordRegExp.test(currentPassword)) {
            setIsPassword(false);
            setPasswordMessage(", ,   8  .")
        } else {
            setPasswordMessage(".");
            setIsPassword(true);
        }
    }, [])

    const onChangePasswordConfirm = useCallback((e) => {

        const confirmPassword = e.target.value;
        setConfirmPw(confirmPassword)

        if (confirmPassword === "") {
            setConfirmPwMessage("");
        } else if (password !== confirmPassword) {
            setConfirmPwMessage("    .")
            // console.log("Password : " + password)
            // console.log("confirmPw : " + confirmPw)
            setIsConfirmPw(false)
        } else {
            setConfirmPwMessage("   .")
            // console.log("Password : " + password)
            // console.log("confirmPw : " + confirmPw)
            setIsConfirmPw(true)
        }
    }, [password])

    const onChangeName = useCallback((e) => {
        // useState           
        // value      
        const currentName = e.target.value;
        setName(currentName);

        if (currentName == "") {
            setNameMessage("")
        } else if (currentName.includes(' ')) {
            setNameMessage('   ')
            setIsName(false)
        } else {
            setNameMessage('')
            setIsName(true)
        }

    }, [])

    const duplicationNumber = () => {
        const phoneRegExp = /^01(?:0|1|[6-9])[.-]?(\d{3}|\d{4})[.-]?(\d{4})$/;
        const result = state.some((userInfo) => { return (userInfo.number === number) });

        if (!phoneRegExp.test(number)) {
            alert(' .');
        } else if (result) {
            alert(' .');
        } else {
            alert('  .');
            setIsNumber(true);
        }
    }

    const onChangeNumber = useCallback((e) => {
        // const phoneRegExp = /^01(?:0|1|[6-9])[.-]?(\d{3}|\d{4})[.-]?(\d{4})$/;
        const currentNumber = e.target.value;
        setNumber(currentNumber)
        setIsNumber(true);
    }, [])

    const onClickDel = (field) => {
        switch (field) {
            case 'id':
                setId("");
                setIdMessage("");
                break;
            case 'password':
                setPassword("");
                console.log(password)
                setPasswordMessage("");
                break;
            case 'confirmPw':
                setConfirmPw("");
                setConfirmPwMessage("");
                break;
            case 'name':
                setName("");
                setNameMessage("");
                break;
            case 'number':
                setNumber("");
                setNumberMessage("");
                break;
            default:
                break;
        }
    }

    return (
        <div className="Join_wrap">
            <div className='Join_title'> ESR .</div>

            <div className='Join_input_wrap'>
                <div className="Join_input Join_id">
                    <input id="id" name="id" placeholder=" (4-12      .)" value={id} onChange={onChangeId} />
                    <img className='cursorPinter' onClick={duplicationId} src={process.env.PUBLIC_URL + '/login_img/dup_check.png'} />
                </div>
                <p className="join_message"> {idMessage} </p>

                <div className='Join_input Join_pw'>
                    <input type="text" value={password} placeholder=" (, ,   8  .)" onChange={onChangePassword} />
                    <img className='cursorPinter' onClick={() => onClickDel('password')} src={process.env.PUBLIC_URL + '/login_img/delete_button.png'} />
                </div>
                <p className="join_message"> {PasswordMessage} </p>

                <div className='Join_input Join_pw'>
                    <input type="text" value={confirmPw} placeholder=" " onChange={onChangePasswordConfirm} />
                    <img className='cursorPinter' onClick={() => onClickDel('confirmPw')} src={process.env.PUBLIC_URL + '/login_img/delete_button.png'} />
                </div>
                <p className="join_message"> {ConfirmPwMessage} </p>

                <div className='Join_input Join_pw'>
                    <input type="text" value={name} placeholder=" (  )" onChange={onChangeName} />
                    <img className='cursorPinter' onClick={() => onClickDel('name')} src={process.env.PUBLIC_URL + '/login_img/delete_button.png'} />
                </div>
                <p className="join_message"> {NameMessage} </p>

                <div className='Join_input Join_pw'>
                    <input type="text" value={number} placeholder=" ( : 010-1234-1234)" onChange={onChangeNumber} />
                    <img className='cursorPinter' onClick={duplicationNumber} src={process.env.PUBLIC_URL + '/login_img/dup_check.png'} />
                </div>
                <p className="join_message"> {NumberMessage} </p>
            </div>

            <div className='join_button_wrap'>
                <Link to="/Welcome">
                    <button type="button" onClick={onClickJoin} className="join_button cursorPinter"></button>
                </Link>
            </div>
        </div>

    )
}

function Welcome() {
    const state = useSelector((state) => { return state.userInfo })
    console.log(state)

    return (
        <div className="welcome_wrap">
            <div className='welcome_title'>!</div>
            <div className='welcome_subtitle'>    ESR.</div>

            <div className='join_button_wrap'>
                <Link to="/">
                    <button type="button" className="join_button cursorPinter"></button>
                </Link>
            </div>
        </div>

    )
}

export { Login, Join, Welcome };